import * as React from 'react';

type Props = {
  onSubmit: ({login, password}: {login: string; password: string}) => void;
  onReplayButtonClick: () => void;
}


class AuthScreen extends React.PureComponent<Props> {
  private loginRef: React.RefObject<HTMLInputElement>;
  private passwordRef: React.RefObject<HTMLInputElement>;

  constructor(props) {
    super(props);

    this.loginRef = React.createRef();
    this.passwordRef = React.createRef();

    this._handleSubmit = this._handleSubmit.bind(this);
  }

  _handleSubmit(evt) {
    const {onSubmit} = this.props;

    evt.preventDefault();

    onSubmit({
      login: this.loginRef.current.value,
      password: this.passwordRef.current.value,
    });
  }

  render() {
    const {onReplayButtonClick} = this.props;

    return (
      <section className="login">
        <div className="login__logo">
          <img src="img/melody-logo.png" alt=" " width="186" height="83" />
        </div>
        <h2 className="login__title">  !</h2>
        <p className="login__text">   ? !</p>
        <form
          className="login__form"
          action=""
          onSubmit={this._handleSubmit}
        >
          <p className="login__field">
            <label className="login__label" htmlFor="name"></label>
            <input className="login__input" type="text" name="name" id="name"
              ref={this.loginRef}
            />
          </p>
          <p className="login__field">
            <label className="login__label" htmlFor="password"></label>
            <input className="login__input" type="text" name="password" id="password"
              ref={this.passwordRef}
            />
            <span className="login__error"> </span>
          </p>
          <button className="login__button button" type="submit"></button>
        </form>
        <button className="replay" type="button"
          onClick={onReplayButtonClick}
        >  </button>
      </section>
    );
  }
}

export default AuthScreen;
