import * as React from 'react';
import {Link} from 'react-router-dom';
import {AppRoute} from '../../const';

type Props = {
  questionsCount: number;
  mistakesCount: number;
  onReplayButtonClick: () => void;
}

const WinScreen: React.FC<Props> = ({questionsCount, mistakesCount, onReplayButtonClick}: Props) => {
  const correctlyQuestionsCount = questionsCount - mistakesCount;

  return (
    <section className="result">
      <div className="result__logo">
        <img src="img/melody-logo.png" alt=" " width="186" height="83" />
      </div>
      <h2 className="result__title">  !</h2>
      <p className="result__total">    {correctlyQuestionsCount}    {mistakesCount} </p>
      <Link
        className="replay"
        to={AppRoute.ROOT}
        onClick={onReplayButtonClick}
      >
          
      </Link>
    </section>
  );
};

export default WinScreen;
